import { createWebHistory, createRouter } from "vue-router"; //라이브러리명 적으면 라이브러리 import해서 쓰는것일뿐
import List1 from './components/List1.vue';///List1.vue가져오기
import home1 from './components/home.vue';
import detail from './components/Detail.vue';
import no404 from './components/404.vue';
import Author from './components/Author.vue';
import Comment from './components/Comment.vue';
const routes = [
    //vue라우터 만든사람이 이렇게 쓰라고한거당~ 외울필요없고 이해만하면된당!
  {
      //누가 /list로 접속하면  <List>보여주자
    path: "/list",
    component: List1,
  },
  {
    //여러개할수두있당~
     //누가 /home로 접속하면  <home>보여주자
     path: "/",
     component: home1,
},
{
   path: "/detail/:id",
   component: detail,
   children: [
     {
       path:"author",//상대경로로 적으셈
       component: Author,
     },
     {
      path:"comment", //상대경로로 적으셈
      component: Comment,
    }
   ]
},
{
  //404페이지 만들어보장 아무문자나 넣으면
   path: "/:anything(.*)",
   component: no404,
}
];
//vue-router 4 사용법대로 createRouter()안에 설정 막 집어넣으면 끝
const router1 = createRouter({
  history: createWebHistory(),
  routes,
});

export default router1; 